.header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  height: 3rem;
  background-color: #3666a0;
  align-content: center;
  justify-content: space-between;
  z-index: 999; }
  .header h1 {
    color: #fff;
    margin: auto 2rem;
    height: 2rem;
    width: 30%; }
  .header__view-test {
    margin: auto 2rem;
    color: #fff;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    /*Hiding the select arrow for IE10*/ }
    .header__view-test select {
      background-color: #e6e6e6;
      color: #2b4141;
      font-size: inherit;
      padding: .5em;
      padding-right: 2.5em;
      min-width: 10rem;
      border: 0;
      margin: 0;
      border-radius: 3px;
      text-indent: 0.01px;
      text-overflow: '';
      /*Hiding the select arrow for firefox*/
      -moz-appearance: none;
      /*Hiding the select arrow for chrome*/
      -webkit-appearance: none;
      /*Hiding the select arrow default implementation*/
      appearance: none; }
    .header__view-test select::-ms-expand {
      display: none; }
    .header__view-test::before, .header__view-test::after {
      content: "";
      position: absolute;
      pointer-events: none; }
    .header__view-test::after {
      /*  Custom dropdown arrow */
      content: "\25BC";
      height: 1em;
      font-size: .625em;
      line-height: 1;
      right: 1.2em;
      top: 50%;
      margin-top: -.5em;
      color: rgba(0, 0, 0, 0.6); }
    .header__view-test::before {
      /*  Custom dropdown arrow cover */
      width: 2em;
      right: 0;
      top: 0;
      bottom: 0;
      border-radius: 0 3px 3px 0;
      background-color: rgba(0, 0, 0, 0.2); }
    .header__view-test select[disabled] {
      color: rgba(0, 0, 0, 0.25); }

.graph-container {
  height: 100%; }

.class-view, .method-view {
  display: block;
  width: 100%;
  overflow: scroll;
  position: relative;
  overflow: scroll; }

.class-view {
  padding: 4rem 2rem 2rem;
  user-select: none; }
  .class-view p {
    margin-bottom: 2rem; }
  .class-view .arrow {
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-right: 20px solid #e6e6e6;
    position: absolute;
    right: 0;
    display: none; }
  .class-view .systemDiv {
    margin: auto;
    width: 100%;
    height: 100%;
    transition: .2s opacity ease-in-out; }
    .class-view .systemDiv svg {
      max-height: 80%; }

.method-view {
  padding: 2rem;
  user-select: none; }
  .method-view > .classDiv {
    opacity: 0;
    position: absolute;
    margin: auto;
    width: 100%;
    height: 100%;
    transition: .2s opacity ease-in-out; }
    .method-view > .classDiv svg {
      max-height: 80%; }
    .method-view > .classDiv.show {
      opacity: 1; }

.method-view .cluster text {
  font-weight: 700; }

.method-view .node .label {
  color: #003863;
  transition: .7s all ease-in-out; }

.method-view .node.clickable {
  cursor: pointer; }

.method-view .node.highlighted rect, .method-view .node.highlighted circle, .method-view .node.highlighted ellipse, .method-view .node.highlighted polygon {
  fill: #ffb238;
  stroke: #f19143;
  transition: .7s all ease-in-out; }

.method-view .node.highlighted .label {
  color: #fff;
  transition: .7s all ease-in-out; }

.method-view .node rect, .method-view .node circle, .method-view .node ellipse, .method-view .node polygon {
  fill: #d9d7e6;
  stroke: #3666a0;
  stroke-width: 1px;
  transition: .7s all ease-in-out; }

.method-view .edgePath.highlighted .path {
  stroke: #f55536; }

.method-view .edgePath.highlighted .arrowheadPath {
  fill: #f55536; }

.method-view .edgePath .path {
  stroke: #3666a0;
  stroke-width: 1.5px;
  transition: .7s all ease-in-out; }

.method-view .edgePath .arrowheadPath {
  fill: #3666a0;
  transition: .7s all ease-in-out; }

.method-view .edgeLabel {
  background-color: #8ab9b5; }

.method-view .cluster rect {
  fill: #e6e6e6 !important;
  stroke: #e6e6e6 !important;
  stroke-width: 1px !important; }

.method-view .cluster text {
  fill: #003863; }

